<template>
    <div class="grid">

        <!--通讯录-->
        <div class="col-4 pad-left-2">
            <div class="search-widget">
                <div class="s-w-inp">
                    <input type="text" placeholder="搜索" class="s-w-inp-text" maxlength="6" v-model="serch_uname">
                </div>
                <span class="s-w-btn" @click="serch_user"><span class="icon-sousuo s-w-sousuo-ico"></span></span>
            </div>
            <div class="pad-h-1"></div>
            <div class="mail-list-widget">
                <div class="ml-w-title">我的通讯录</div>
                <ul class="ml-w-ul" v-if="mail_list.length > 0">
                    <li class="ml-w-li" v-for="user of mail_list" :state="user.state" :id="user.uid" @click="sel_user">
                        <head-widget :uname="user.name"></head-widget>
                        <span v-text="user.suffix?user.suffix:user.name"></span>
                    </li>
                </ul>
                <div v-else class="no-result-widget">
                    <span class="icon-nodata nr-w-ico"></span>
                    <span class="nr-w-text">未找到该用户</span>
                </div>
            </div>
        </div>

        <div class="col-8">

            <!--编辑器-->
            <div class="editor-widget">
                <div class="e-w-utils grid">
                    <div class="e-w-util-foot col-12">
                        <span class="e-w-tab e-w-util-tab"><span class="icon-biaoqing"></span></span>
                        <span class="e-w-tab e-w-util-tab"><span class="icon-fujian"></span></span>
                        <span class="e-w-util-tab"><span class="e-w-send-btn" @click="send_msg">发送</span></span>
                    </div>
                </div>
                <div class="e-w-cont">
                    <div id="EditorCont" contenteditable="true" class="e-w-cont-edit"></div>
                </div>
            </div>

            <div class="pad-h-4"></div>

            <!--聊天记录-->
            <div class="chat-record-widget pad-w-2">
                <ul>
                    <li v-for="record of chat_record.record"
                        :class="{'cr-w-item':true, 'cr-w-item-left':record.from === 'you', 'cr-w-item-right':record.from === 'me'}">
                        <div>
                            <span :class="{'head-widget':true, 'h-w-green':record.from !== 'you'}" v-text="record.from !== 'you'? user.uname: chat_record.you"></span>
                        </div>
                        <div class="cr-w-cont-box">
                            <span>{{record.desc}}</span>
                            <video v-if="record.type == 'video'" controls class="cr-w-video">
                                <source src="/static/res/video/video_2.mp4"/>
                            </video>
                        </div>
                    </li>
                </ul>
            </div>

        </div>

    </div>
</template>

<script>
    import Head from "@/components/widget/head-widget";

    export default {
        name: "editor-widget",
        props: ["mail-list"],
        data() {
            return {

                /*通讯录*/
                mail_list: this.mailList,

                /*搜索用户名*/
                serch_uname: "",

                /*聊天记录*/
                chat_record: {
                    uid: "",
                    you: "",
                    record: []
                },

            }
        },
        methods: {

            /*搜索用户*/
            serch_user() {
                if(!this.serch_uname){//无搜索字段，返回全部
                    this.mail_list = this.mailList;
                }else{
                    this.mail_list = [];
                    this.mailList.forEach(user => {
                        if(user.name === this.serch_uname){//有结果
                            this.mail_list.push(user);
                            return;
                        }
                    })
                }
                this.serch_uname = "";
            },

            /*切换对话用户*/
            sel_user(e) {
                let vue = this;
                this.mailList.forEach(function (user) {
                    let uid = user.uid;
                    if (user.state === "act") user.state = "";
                    if (uid == e.currentTarget.id) {
                        user.state = "act";

                        /*????请求获取聊天记录*/
                        vue.chat_record = {
                            uid: "u1",
                            you: "语泽",
                            record: [
                                {
                                    "from": "you",
                                    "type": "video",
                                    "video": "/static/res/video/t.mp4",
                                    "desc": "你可以看下这个视频：如何解析http协议？"
                                },
                                {
                                    "from": "me",
                                    "type": "text",
                                    "desc": "张老师，虽然学习了http的概念，但是我还是不知道http协议是怎么发生效果的？你能再讲解一下吗？"
                                },
                                {
                                    "from": "me",
                                    "type": "text",
                                    "desc": "您好张老师，有个问题想请教你"
                                },
                            ]
                        };

                    }
                })
            },

            /*发送消息*/
            send_msg() {
                let $editor_cont = document.querySelector("#EditorCont"),
                    inp_cont = $editor_cont.innerHTML;
                $editor_cont.innerHTML = "";

                /*????发送消息到服务端*/
                if(inp_cont && this.chat_record.uid){
                    this.chat_record.record.unshift({
                        "from": "me",
                        "type": "text",
                        "desc": inp_cont
                    });
                }

            }

        },
        watch: {
            mailList(classmate){
                this.mailList = this.mail_list = classmate;
            }
        },
        computed: {

            /*用户信息*/
            user(){
                return this.$store.state.user
            }

        },
        components: {
            "head-widget": Head,
        }
    }
</script>

<style scoped>
    @import "../../css/editor-widget.css";
    @import "../../css/head-widget.css";
    @import "../../css/search-widget.css";
    @import "../../css/mail-list-widget.css";
    @import "../../css/chat-record-widget.css";
    @import "../../css/no-result-widget.css";
</style>


